window.$ = window.jQuery = function (selectorOrArrayOrTemplate) {
    let elements
    if (typeof selectorOrArrayOrTemplate === 'string') {
        if (selectorOrArrayOrTemplate[0] === '<') {
            // ÂàõÂª∫div
            elements = [createElement(selectorOrArrayOrTemplate)]
        } else {
            // Êü•Êâædiv
            elements = document.querySelectorAll(selectorOrArrayOrTemplate)
        }
    } else if (selectorOrArrayOrTemplate instanceof Array) {
        elements = selectorOrArrayOrTemplate
    }

    function createElement(string) {
        const container = document.createElement("template")
        container.innerHTML = string.trim()
        return container.content.firstChild
    }
    const api = Object.create(jQuery.prototype) // ÂàõÂª∫‰∏Ä‰∏™ÂØπË±°ÔºåËøô‰∏™ÂØπË±°ÁöÑ__proto__‰∏∫Êã¨Âè∑ÈáåÈù¢ÁöÑ‰∏úË•ø
    // api.elements = elements
    // api.oldApi = selectorOrArrayOrTemplate.oldApi
    Object.assign(api, {
        elements: elements,
        oldApi: selectorOrArrayOrTemplate.oldApi
    })
    return api
}

jQuery.fn = jQuery.prototype = {
    jquery: true,
    constructor: jQuery,
    parent() {
        const array = []
        this.each((node) => {
            if (array.indexOf(node.parentNode) === -1){
                array.push(node.parentNode)
                }
            
        })
        return jQuery(array)
    },
    get(index) {
        return this.elements[index]
    },
    appendTo(node) {
        if (node instanceof Element) {
            this.each(el => node.appendChild(el)) // ÈÅçÂéÜelementsÔºåÂØπÊØè‰∏™elËøõË°ånode.appendChild
        } else if (node.jquery === true) {
            this.each(el => node.get(0).appendChild(el)) // ÈÅçÂéÜelementsÔºåÂØπÊØè‰∏™elËøõË°ånode.get(0).appendChild
        }
    },
    append(children) {
        if (children instanceof Element) {
            this.get(0).appendChild(children)
        } else if (children instanceof HTMLCollection) {
            for (let i = 0; i < children.length; i++){
                this.get(0).appendChild(children[i])
            }
        } else if (children.jquery === true) {
            children.each(node => this.get(0).appendChild(node))
        }
    },
    children() {
        const array = []
        this.each((node) => {
                array.push(...node.children) // ÂèØ‰ª•ÊääÂÖÉÁ¥†Èì∫ÂºÄ
            })
        return jQuery(array)
    },
    each(fn) {
        for (let i = 0; i < elements.length; i++){
            fn.call(null, elements[i], i)
        } 
        return this
    },
    print() {
        console.log(elements)
    },
    // Èó≠ÂåÖÔºöÂáΩÊï∞ËÆøÈóÆÂ§ñÈÉ®ÁöÑÂèòÈáè
    addClass(className) {
        for (let i = 0; i < elements.length; i++){
            elements[i].classList.add(className)
        }
        return this
    },
    find(selector) {
        let array = []
        for (let i = 0; i < elements.length; i++){
            array = array.concat(Array.from(elements[i].querySelectorAll(selector)))
        }
        array.oldApi = this // thisÊòØÊóßapi
        console.log(array)
        console.log(jQuery(array))
        return jQuery(array)
    },
    end() {
        return this.oldApi // thisÂ∞±ÊòØÊñ∞api
    },
}